import * as React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import { Container, Box, Heading } from "../components/ui"
import schema from "../imgs/schema.png"
import filter from "../imgs/filter.png"
import slam from "../imgs/slam.png"
import tempgrpah from "../imgs/temp_graph.png"

export default function Page(props) {
  const { page } = props.data
  const imageStyle = {
    maxWidth: '800px'
  }

  return (
    <Layout {...page}>
      <Box paddingY={5}>
        <Container width="narrow">
          <Heading as="h1">{page.title}</Heading>
          <div>
            {page.title === "Research Process" ? (
              <div>
              <img src={schema} style={imageStyle} />
              </div>
            ) : (
              <br />
            )}
          </div>
          <div
            dangerouslySetInnerHTML={{
              __html: page.html,
            }}
          />
          <div>
            {page.title === "Research Process" ? (
              <div>
                <img src={slam} style={imageStyle} />
                <img src={tempgrpah} style={imageStyle} />
                <img src={filter} style={imageStyle} />
              </div>
            ) : (
              <br />
            )}
          </div>
        </Container>
      </Box>
    </Layout>
  )
}

export const query = graphql`
  query PageContent($id: String!) {
    page(id: { eq: $id }) {
      id
      title
      slug
      description
      image {
        id
        url
      }
      html
    }
  }
`
